<template>
	<div class="main">
		<view class="input-box">
			<input placeholder="默认关键字" 		
			placeholder-style="color:#c0c0c0;" 
			focus
			style="width:81%;"
			v-model="keyCode" confirm-type="确定" 
			@confirm="search(keyCode)" />
			<view class="icon search" @click="search(keyCode)" ></view>
		</view>
		<view>
			<view class="sessionText" v-if="searchSession.length>0">
				<text>历史纪录</text>
				<text @click="clearSession">清除</text>
			</view>
			<view class="tagList">
				<text class="tag" 
				v-for="(ss,index) in searchSession"
				:key="index"
				 @tap="search(ss)">{{ss}}</text>
			</view>
			<text style="font-size: 28upx;">其他人在看</text>
			<view class="tagList">
				<text class="tag" 
				v-for="(item,index) in foodList"
				:key="index"
				 @tap="search(item)">{{item}}</text>
			</view>
		</view>
	</div>
</template>

<script>

export default {
	name: 'searchView',
	components: {
		//组件模板
	},
	props: {
		//组件道具（参数）
		/* ****属性用法*****
		 *
		 * 传递类型 type: Array | Number | String | Object
		 * 为必传 required: true
		 * 默认值 default: ''
		 *
		 */
	},
	data() {
		//数据
		return {
			searchSession:[],//历史纪录
			foodList:['蛋黄酥','烧肉粽','牛奶糖','三只松鼠坚果大礼包','百草味猪肉干','鳄鱼生鲜','糖'],
			keyCode: ''
		};
	},
	onReachBottom() {
		uni.showToast({ title: '触发上拉加载' });
	},
	methods: {
		//方法
		search(code) {
			if(code!==''){
				this.keyCode = code;
				uni.showToast({ title: '加载中...',icon:'loading'});
				setTimeout(() =>{
					uni.hideLoading();
					uni.navigateTo({
						url: '../../goodPage/goods/goods-list?cid='+Math.random()+'&&name='+code
					});
					if(this.searchSession.indexOf(code)<0){
						this.searchSession.push(code);
						uni.setStorage({
							key: 'searchSession',
							data: this.searchSession,
							success: () =>{
								console.log('success');
							}
						});
					}

				}, 2000);
			}

		},
		toGoods(goods){
			uni.navigateTo({
				url: '../../goodPage/goods/goods?cid='+goods.goods_id+'&&name='+goods.name+'&&img='+goods.img+'&&price='+goods.price
			});
		},
		clearSession(){
			this.searchSession = [];
			uni.removeStorage({
				key: 'searchSession',
				success:(res) =>{
					console.log('success');
				}
			});
		}
	},
	computed: {
		//计算属性
	},
	watch: {
		//监测数据变化
	},

	//===================组件钩子===========================

	created() {
		//实例被创建完毕之后执行
	},
	mounted() {
		uni.getStorage({
			key: 'searchSession',
			success: (res) =>{
				this.searchSession = res.data;
			}
		});
	}
};
</script>

<style lang="scss">
@font-face {
	font-family: 'HMfont-home';
	src: url('data:application/x-font-woff2;charset=utf-8;base64,')
		format('woff2');
}
.main {
	margin: 0 5%;
}
.input-box {
	width: 100%;
	height: 65upx;
	margin-top: 20upx;
	background-color: #f5f5f5;
	border-radius: 30upx;
	position: relative;
	display: flex;
	align-items: center;
	.icon {
		display: flex;
		align-items: center;
		position: absolute;
		top: 0;
		right: 0;
		width: 60upx;
		height: 60upx;
		font-size: 34upx;
		color: #c0c0c0;
	}
	input {
		padding-left: 28upx;
		height: 28upx;
		font-size: 28upx;
	}
}
.tagList {
	margin-top: 20upx;
	display: flex;
	flex-wrap: wrap;
	.tag {
		padding: 7upx;
		margin: 5upx;
		color: skyblue;
		font-size: 28upx;
		border: 1upx solid #c0c0c0;
		border-radius: 15upx;
	}
}
.icon {
	font-family: 'HMfont-home' !important;
	font-size: 60upx;
	font-style: normal;
	color: #000000;
	&.yuyin {
		&:before {
			content: '\e64e';
		}
	}
	&.tongzhi {
		&:before {
			content: '\e729';
		}
	}
	&.search {
		&:before {
			content: '\e628';
		}
	}
	&.location {
		&:before {
			content: '\e611';
		}
	}
	&.xia {
		&:before {
			content: '\e689';
		}
	}
}

.sessionText{
	font-size: 28upx;
	margin: 3upx;
	display: flex;
	justify-content: space-between;
	text{
		padding: 8px 0;
		color:#c0c0c0;
	}
}
</style>
